
.app-wrapper{
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-areas: 'nik nik'
                        'avatar message'
                        'addName addName'
                        'names names';
  /*grid-template-rows: 100px 1fr;*/
  grid-template-columns: 2fr 8fr;
  font-family: Arial;
}
.counter{
  margin: 10px;
  padding: 10px 14px;
  border: 1px solid #000000;
  background-color: lightskyblue;
}

.error{
  border-color: red;
}
.menu{
  position: relative;
}
.menu-items{
  padding: 0;
  margin: 0;
}
.menu-list {
  list-style: none;
}
.menu-link{
  text-decoration: none;
  color: #555;
  display: flex;
  padding: 10px 16px;
  border-bottom: 1px solid #555;
}
.menu-button{
  position: relative;
  z-index: 99;
  font-size: 26px;
  cursor: pointer;
}
.nav{
  width: 20vw;
  border: 1px solid #888;
  position: absolute;
  top: 0px;
  z-index: 999;
  background-color: #ffff;
  left: -50vw;
  transition: left 0.3s ease;
}
.nav.show{
  left: 0;
}
.menu-close{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  cursor: pointer;
  font-size: 20px;
  padding: 7px 7px 7px 0;
}
.active{
  color: red;
}
.status{
  padding: 20px;
  border: 1px solid #555555;
  border-radius: 5px;
  background-color: rgba(0,0,0, 0.05);
  display: flex;
  flex-direction: column;
}

.todoList > div {
  padding: 10px;
}

.todoList-task {

}
.filter-active {
  background-color: blue;
}
.error {
  border-color: red;
}
.todoList-task.done {
  opacity: 0.5;
}
.tuesday{
  display: flex;
}
.louder {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}
